import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

import Login from '../views/login/Login'
import Admin from '../views/admin/Admin'
//
import MovieHome from '../views/admin/MovieHome'
import UserManager from '../views/admin/UserManager'
import MovieHallDetail from '../views/admin/MovieHallDetail'


//
import Performance from '../views/ticket/Performance'
import Search from '../views/ticket/Search'
import Sale from '../views/ticket/Sale'
import SellTicket from '../views/ticket/SellTicket'
import OutTicket from '../views/ticket/OutTicket'

import Ticket from '../views/ticket/Ticket'
import Manager from '../views/manager/Manager'
//
import MovieManager from '../views/manager/MovieManager'
import PromancePlan from '../views/manager/PromancePlan'
import SaleData from '../views/manager/SaleData'


// - 
import Users from '../views/users/Users'

// - 
import Introduction from '../views/cinema-Introduction/Introduction'

// - 
import Movieplan from '../views/movieTable/Movieplan'
// -  -  
import Detail from '../views/detail/Detail'



// - 
import Profile from '../views/profile/Profile'
Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/admin',
    name: 'Admin',
    component: Admin,
    redirect:'/admin/movies-home',
    children: [{
      path: 'movies-home',
      component: MovieHome,
      name: '',

    }, {
      path: 'user-manager',
      component: UserManager,
      name: '',

    },
    {
      path: 'movieHallDetail',
      component: MovieHallDetail,
      name: '',

    }]
  },
  {
    path: '/ticket',
    name: 'Ticket',
    component: Ticket,
    redirect:'/ticket/performance',
    children: [{
      path: 'performance',
      component: Performance,
      name: '',

    }, {
      path: 'search',
      component: Search,
      name: '',

    },
    {
      path: 'sale',
      component: Sale,
      name: '',
      
    },
    {
      path: 'sellTicket',
      component: SellTicket,
      name: '',

    },
    {
      path: 'outTicket',
      component: OutTicket,
      name: '',

    },]
  }, 
  
  {
    path: '/manager',
    name: 'Manager',
    component: Manager,
    redirect:'/manager/movie-manager',
    children: [{
      path: 'movie-manager',
      component: MovieManager,
      name: '',

    }, {
      path: 'promance-plan',
      component: PromancePlan,
      name: '',

    },
    {
      path: 'saleData',
      component: SaleData,
      name: '',

    }]
  },{
    path: '/users',
    name: 'Users',
    component: Users,
  },
  {
    path: '/introduction',
    name: 'Introduction',
    component: Introduction,
  },
  {
    path: '/movieplan',
    name: 'Movieplan',
    component: Movieplan,
  },
  {
    path: '/profile',
    name: 'Profile',
    component: Profile,
  },
  {
    path: '/ticketDetail',
    name: 'Detail',
    component: Detail,
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router