<template>
  <div class="home1">
    <div class="top1">
      <span>{{title}}</span>
      <label for @click="tab"></label>
    </div>
    <div class="bottom1">
      <div class="biaoge" :style="{opacity:formShow}">
        <el-table
          :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
          stripe
          style="width: 100%"
        >
          <el-table-column prop="movieName" label="" width="180"></el-table-column>
          <el-table-column prop="movieDate" label=""></el-table-column>
          <el-table-column prop="movieClass" label=""></el-table-column>
          <el-table-column prop="movieState" label=""></el-table-column>
          <el-table-column prop="movieMoney" label="()" width="180"></el-table-column>
          <el-table-column prop="address" label="">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="handleClick2(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <template>
          <div class="page">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[2,3 , 4, 5]"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="tableData.length"
            ></el-pagination>
          </div>
        </template>
      </div>
      <div class="picture" :style="{opacity:pictureShow}">
        <div ref="echarts" class="simpleDemo"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { filmsList } from "../../network/films";

//echarts
import echarts from "echarts";

export default {
  data() {
    return {
      optionData: [],
      tableData: [],
      currentPage: 1,
      pagesize: 5,
      currpage: 1,
      title: "",
      formShow: 1,
      pictureShow: 0,
      form: {
        searchName: ""
      },
      //echarts

      //echarts
      chart: null,
      option: {
        backgroundColor: "#45515a", //
        //
        title: {
          text: "",
          subtext: "",
          left: "center",
          top: 20,
          textStyle: {
            color: "#ccc",
            fontStyle: "italic" //
          }
        },
        //
        tooltip: {
          trigger: "item", //
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        //
        legend: {
          orient: "vertical",
          left: "left",
          textStyle: {
            color: "#c8c8d0"
          },
          data: [
            "",
            "",
            "",
            "",
            "",
            ""
          ] //name
        },
        //
        toolbox: {
          show: true, //
          feature: {
            //dataZoom:{show:true}, //
            saveAsImage: { show: true } //
            //magicType:{type:['line','bar']}//
          }
        },
        //
        series: [
          {
            name: "",
            type: "pie",
            radius: "55%",
            center: ["50%", "50%"],
            data: [],

            roseType: "radius", //'area'
            label: {
              //
              normal: {
                //normal
                textStyle: {
                  color: "rgba(255, 255, 255, 0.3)"
                }
              }
            },
            labelLine: {
              //
              normal: {
                lineStyle: {
                  color: "rgba(255, 255, 255, 0.3)"
                },
                smooth: 0.5, //0-1
                length: 10, //
                length2: 20 //
              }
            },
            width: "auto",
            height: "auto",
            itemStyle: {
              //
              normal: {
                //color: '#97413c',
                shadowBlur: 50, //
                shadowColor: "rgba(0, 0, 0, 0.5)" //
              }
            },
            animationType: "scale", //scaleexpansion
            animationEasing: "elasticOut", //
            animationDelay: function(idx) {
              //idx
              return Math.random() * 200;
            }
          }
        ],
        color: [
          "#CCFF33",
          "#CCCCFF",
          "#CC99FF",
          "#CC9900",
          "#CC6633",
          "#33CCFF",
          "#CC3300",
          "#9933FF"
        ]
      }
    };
  },
  created() {
    filmsList().then(res => {
      console.log(res);
      var arr1 = [];
      for (let i in res) {
        if (res[i].movieState == "") {
          arr1.push(res[i]);
        }
      }
      // console.log(arr1);
      this.tableData = arr1;
      console.log(this.option.series[0].data);
      var movieNameList = [];
      // var optionData= [];
      for (let i in res) {
        if (res[i].movieState == "") {
          movieNameList.push(res[i].movieName);
          var obj = {};
          obj.name = res[i].movieName;
          obj.value = parseFloat(res[i].movieMoney);
          this.optionData.push(obj);
        }
      }
      console.log(this.optionData);
      this.option.series[0].data = this.optionData;
      this.option.legend.data = movieNameList;
      this.getPage();
    });
  },
  methods: {
    getPage() {
      this.chart = echarts.init(this.$refs.echarts);
      //  this.option.series[0].data = this.optionData;
      // 
      this.chart.setOption(this.option);
    },
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    tab() {
      if (this.title == "") {
        this.title = "";
        this.formShow = 0;
        this.pictureShow = 1;
      } else {
        this.title = "";
        this.formShow = 1;
        this.pictureShow = 0;
      }
    }
  }
};
</script>

<style>
.home1 {
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
  box-sizing: border-box;
  position: relative;
}

.top1 {
  width: 100%;
  height: 70px;
  line-height: 70px;
  /* border: 2px solid red; */
  color: #409eff;
  font-size: 26px;
  font-family: "";
  border-bottom: 2px solid rgb(224, 221, 221);
}

.top1 label {
  color: rgb(170, 167, 152);
  font-size: 20px;
  cursor: pointer;
  text-decoration: underline;
}

.top1 label:hover {
  color: gold;
}

.bottom1 {
  position: relative;
  /* margin-top: 30px; */
  width: 100%;
  height: calc(100% - 70px);
  /* border: 2px solid green; */
}

.page {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50px;
}

.biaoge {
  position: absolute;
  width: 100%;
  height: 100%;
}

.picture {
  position: absolute;
  width: 100%;
  height: 100%;
  /* position: relative; */
  /* border: 2px solid green; */
  box-sizing: border-box;
  display: flex;
}

.simpleDemo {
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
  box-sizing: border-box;
}
</style>